<template>
    <div class="supply-wrap">

        <div class="item-box">
            <div class="item-title">
                你共有 <span class="important">{{pinchinInfo.signCardNum}}张</span> 补签卡
            </div>
            <div class="title-desc">
                使用补签卡，对未打卡的天数进行补签
                <div class="img-desc-box">
                    <img class="img-desc" 
                     src="./imgs/icon-question.png" @click="showDescCont">
                </div>
            </div>

            <div class="img-content" :style="{backgroundImage: `url(${imgsUrl.bg1})`}">
                <div class="tag">早安打卡</div>
                <div class="bottom-text">坚持别人坚持不了的坚持,你就会成就别人成就不了的成就</div>
            </div>

            <div class="btn-box">
                <!-- <div class="btn"></div> -->
                <div v-if="Number(pinchinInfo.signCardNum)>0" class="btn" @click="toSupply">进行补签</div>
                <div v-else class="btn disabled">进行补签</div>
                <span class="text"></span>
            </div>
        </div>

        <div class="item-box">
            <div class="item-title">
                <span class="important">如何获得补签卡？</span>
            </div>
            <div class="title-desc">完成当月的累计打卡天数，获得补签卡</div>

            <div class="img-content" :style="{backgroundImage: `url(${imgsUrl.bg2})`}">

                <div class="bottom-text">人生就像一场旅行，在乎的不是终点，而是沿途的风景</div>
            </div>

            <div class="btn-box">
                <div class="btn" @click="toXinLu">去获得</div>
                <span class="text">完成人生的 “心路历程”，即可获得补签卡</span>
            </div>
        </div>
        <Dialog :visable.sync="visable" v-if="visable"></Dialog>
    </div>
</template>

<script>
    import { clog } from "../../api/punchin";
    import SignCardMixin from "../commonMixins/SignCardMixin";
    import Dialog from "./components/Dialog";
    export default {
        name: "supplyPunchin",
        components: { Dialog},
        mixins: [SignCardMixin],
        data() {
            return {
                imgsUrl: {
                    bg1: require('./imgs/supply-bg1.jpg'),
                    bg2: require('./imgs/supply-bg2.jpg')
                },
                visable: false
            }
        },
        methods: {
            showDescCont() {
                this.visable = true
            },
            toSupply() {
                this.$router.push('/history')
                try {
                    clog("早安打卡", [
                        "vjf-h5-log",
                        "transTime",
                        "openid",
                        "好物好生活平",
                        location.href.split("?")[0],
                        "点击去补签",
                        "进行补签页点击去补签"
                    ]);
                    _hmt.push(["_trackEvent", "click", '进行补签页', "点击去补签"]);
                } catch(e) {
                    console.log('埋点失败' + e);        
                }
            },
            toXinLu() {
                this.$router.push('/punchin-progress')
                try {
                    clog("早安打卡", [
                        "vjf-h5-log",
                        "transTime",
                        "openid",
                        "好物好生活平",
                        location.href.split("?")[0],
                        "点击去获得",
                        "进行补签页点击去获得"
                    ]);
                    _hmt.push(["_trackEvent", "click", '进行补签页', "点击去获得"]);
                } catch(e) {
                    console.log('埋点失败' + e);        
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .supply-wrap {
        overflow: hidden;
        padding-bottom: 20px;
        .item-box {
            padding: 0 20px;
            box-sizing: border-box;
            &:nth-of-type(1) {
                margin-top: 20px;
            }
            &:nth-of-type(2) {
                margin-top: 50px;
            }

            .item-title {
                height: 56px;
                display: flex;
                align-items: center;
                font-size: 26px;
                color: #000;
                padding-left: 10px;

                .important {
                    font-size: 36px;
                    font-weight: bold;
                }
            }

            .title-desc {
                height: 48px;
                display: flex;
                align-items: center;
                padding-left: 10px;
                font-size: 26px;
                color: #333333;
                position: relative;
                .img-desc-box { 
                    height: 36px;
                    width: 60px;
                    position: absolute;
                    top: 0;
                    right: 0;
                }
                .img-desc {
                    height: 36px;
                    width: 36px;
                    float: right;
                }
            }

            .img-content {
                width: 100%;
                height: 320px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: 100%;
                position: relative;
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
            }

            .tag {
                width: 150px;
                height: 34px;
                background-color: rgb(233, 51, 37);
                border-top-right-radius: 17px;
                border-bottom-right-radius: 17px;
                text-indent: 30px;
                position: absolute;
                left: -20px;
                top: 22px;
                display: flex;
                align-items: center;
                color: #fff;
                font-size: 24px;
            }

            .bottom-text {
                width: 710px;
                height: 62px;
                display: flex;
                align-items: center;
                color: #fff;
                font-size: 24px;
                text-indent: 10px;
                background-color: rgb(0, 0, 0, 0.5);
                position: absolute;
                bottom: 0;
            }

            .btn-box {
                width: 710px;
                height: 120px;
                background-color: #fff;
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                justify-content: space-between;

                .btn {
                    width: 184px;
                    height: 58px;
                    border-radius: 8px;
                    margin-right: 10px;
                    background-image: linear-gradient( 113deg, rgb(233,51,37) 0%, rgb(222,67,97) 100%);

                    // background-color: #e8342a;
                    color: #fff;
                    font-size: 28px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &.disabled{
                        background-image: none;
                        background-color: #a4a4a4;
                    }
                }

                .text {
                    color: #000;
                    font-size: 24px;
                    text-indent: 10px;
                }

            }
        }
    }

</style>