import { mapState, mapMutations } from 'vuex'
import { punchinCard } from '../../api/punchin';
export default {
  computed: {
    ...mapState(['pinchinInfo']),
  },
  methods: {
    ...mapMutations(['setPinchinInfo']),
    getSignCardNum() {
      if (this.pinchinInfo.signCardNum !== null) return
      return punchinCard().then(res => {
        
        if (res.result.code === '0' && res.result.businessCode === '0') {
          let signCardNum = 0
          if (res.reply) {
            signCardNum = res.reply.signCardNum
          }
          this.setPinchinInfo({
            signCardNum
          })
          return { signCardNum }
        } else {
          this.setPinchinInfo({
            signCardNum: 0
          })
        }
      })
    }
  },
  created() {
    this.getSignCardNum()
  }

}